.loader {
  width: 64px;
  height: 64px;
  background-color: rgba(0, 0, 0, .5);
  position: relative;
  top: 50vh;
  left: 50vw;
  overflow: hidden;
  transform: rotate(45deg);
}

.loader:after {
  content: "";
  background: #222b32;
  margin: auto;
  position: absolute;
  top: 8px;
  bottom: 8px;
  left: 8px;
  right: 8px;
}

.loader:before {
  content: "";
  background: #de3500;
  margin: auto;
  animation: diamondLoader 2s linear infinite;
  position: absolute;
  top: -15px;
  bottom: -15px;
  left: -15px;
  right: -15px;
}

@keyframes diamondLoader {
  0%, 10% {
    transform: translate(-64px, -64px)rotate(-45deg);
  }

  90%, 100% {
    transform: translate(0)rotate(-45deg);
  }
}

